import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { Menu } from "antd";

const items = [
  {
    label: "Home",
    key: "/",
  },
  {
    label: "Research",
    key: "SubMenu",
    children: [
      {
        label: "AI & Imaging Technologies",
        key: "/direction",
      },
      {
        label: "Scientific Applications",
        key: "/application",
      },
    ],
  },
  {
    label: "Publication",
    key: "/publication",
  },
  {
    label: "Team",
    key: "/team",
  },
  {
    label: "Teaching",
    key: "/teaching",
  },
  {
    label: "Contact",
    key: "/contact",
  },
];

const App = () => {
  const [current, setCurrent] = useState("mail");
  const navigate = useNavigate();

  const onClick = (e) => {
    setCurrent(e.key);
    navigate(e.key);
  };

  return (
    <div
      style={{
        padding: "10px 30px",
        display: "flex",
        alignItems: "center",
        justifyContent: "space-between", // 左右两侧对齐
      }}
    >
        <div style={{ display: "flex", alignItems: "center" }}>
    <img
      src="/rawdata/logo_temp.png"
      alt="Logo"
      style={{
        height: "80px",
        marginRight: "10px",
        padding: "0px",
        marginTop: "-10px", // 通过负 margin 消除 padding 的影响
        marginLeft: "0px", // 适配父容器的 padding
      }}
    />
  </div>

      {/* 右侧 Menu */}
      <Menu
        onClick={onClick}
        selectedKeys={[current]}
        mode="horizontal"
        items={items}
        style={{
          display: "inline-block", // 菜单宽度只占文字的宽度
          borderBottom: "none", // 去掉默认的下划线
        }}
        className="text-xl"
      />
      <style>
        {`
          .ant-menu-horizontal > .ant-menu-item::after {
            border-bottom: 2px solid #1890ff; /* 下划线颜色 */
            transform: scaleX(1); /* 设置下划线长度与文字匹配 */
          }
          .ant-menu-horizontal > .ant-menu-item {
            margin: 0 8px; /* 调整文字间距 */
          }
        `}
      </style>
    </div>
  );
};

export default App;
